.header-wrapper {
  .header-title:hover {
    .santa-hat {
      animation: jiggle 0.4s ease-in-out infinite;
    }
  }
  .santa-hat {
    width: 20px;
    height: 20px;
    z-index: 1;
    translate: var(--fi-santa-hat-offset, 8px 2px);
    position: absolute;
    animation: jiggle 0.4s ease-in-out;

    + img.logo {
      // Simulates the effect of a hat covering
      filter: drop-shadow(0.5px -0.5px 1px rgba(var(--fi-santa-hat-shadow, 0, 0 , 0), 0.6));

      [data-theme='dark'] & {
        --fi-santa-hat-shadow: 255, 255 , 255;
      }
    }
  }

  @keyframes jiggle {
    0%, 100% {
      transform: rotate(0deg);
    }
    25% {
      transform: rotate(-8deg);
    }
    50% {
      transform: rotate(6deg);
    }
    75% {
      transform: rotate(-4deg);
    }
  }
}
